.searchbar {
  width: 100%;
  height: 80px;
  background-color: var(--bg);
  display: flex;
  justify-content: center;
  align-items: center;
}

:root {
  --rad: 0.7rem;
  --dur: 0.3s;
  --color-dark: #1f1e1e;
  --color-light: #fff;
  --color-brand: #57bdac;
  --font-fam: 'Lato', sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
  --hue: 223;
  --bg: hsl(var(--hue), 90%, 95%);
}

form {
  position: relative;
  width: 280px;
  background: var(--color-brand);
  border-radius: var(--rad);
}
input,
.search__btn {
  height: var(--height);
  font-family: var(--font-fam);
  border: 5px;
  color: var(--color-dark);
  font-size: 1.8rem;
}
input[type='search'] {
  outline: 0;
  width: 100%;
  background: var(--color-light);
  padding: 0 1.6rem;
  border-radius: var(--rad);
  appearance: none;
  transition: all var(--dur) var(--bez);
  transition-property: width, border-radius;
  z-index: 1;
  position: relative;
  height: 40px;
}
.search__btn {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: var(--btn-width);
  font-weight: bold;
  background: var(--color-brand);
  border-radius: 0 var(--rad) var(--rad) 0;
  height: 40px;
}
input:not(:placeholder-shown) {
  border-radius: var(--rad) 0 0 var(--rad);
  width: calc(100% - var(--btn-width));
  + .search__btn {
    display: block;
  }
}
label {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
